import PdfEmbed from '../components/legal/PdfEmbed';
import termsFile from '../assets/pdf/inBuild_Terms_of_Service.pdf';

function TermsOfService() {
    return (
        <div id="terms-of-service">
            <PdfEmbed header="Terms of Service" file={termsFile} />
        </div>
    )
}

export default TermsOfService
