import PdfEmbed from '../components/legal/PdfEmbed';
import privacyFile from '../assets/pdf/inBuild_Privacy_Policy.pdf';

function PrivacyPolicy() {
    return (
        <div id="privacy-policy">
            <PdfEmbed header="Privacy Policy" file={privacyFile} />
        </div>
    )
}

export default PrivacyPolicy
