import { BrowserRouter as Router } from 'react-router-dom';
import Routes from './Routes';

import Footer from './components/footer/Footer';
import ContactModal from './components/contactForm/ContactModal';
import Navbar from './components/Navbar';

function App() {
  return (
    <Router basename={ process.env.PUBLIC_URL }>
      <div className="container-fluid">
        <ContactModal />
        <Navbar />
        
        <Routes />
      
        <Footer
            menus={[
              {
                title: "Information",
                links: [
                  {
                    nav: true,
                    href: "/",
                    text: "Home"
                  },
                  {
                    nav: true,
                    href: "/product",
                    text: "About inBuild"
                  }
                ]
              },
              {
                title: "inBuild Access",
                links: [
                  {
                    href: "https://app.inbuild.ai/account/login",
                    text: "Sign In"
                  },
                  {
                    modal: true,
                    href: "",
                    text: "Request a Demo"
                  }
                ]
              }
            ]}
            social={[
              {
                href: "https://www.linkedin.com/company/inbuildai",
                faIcon: "linkedin"
              },
              {
                href: "https://www.twitter.com",
                faIcon: "twitter"
              }
            ]}
        />
      </div>
    </Router>
  );
}

export default App;
