import Hero from '../components/hero/Hero';
import Breakout from '../components/breakout/Breakout';
import Content from '../components/content/Content';
import TriColumn from '../components/TriColumn';

import homeImg1 from '../assets/homepage_img_01.png';
import homeImg2 from '../assets/homepage_img_02.png';

function Home() {
    return (
        <div className="homepage">
            <Hero
                header="Software that makes inputting invoices easy with AI."
                subtext="By using our interface to review and approve invoices, you can reduce the time and cost of processing them to less than one minute per invoice, by removing the need for repetitive manual input."
                subheaders={[
                {
                    header: "Automation",
                    body: "Connect Your AP Email Address"
                },
                {
                    header: "Accuracy",
                    body: "97.5% Confidence Threshold"
                },
                {
                    header: "Integration",
                    body: "Quickbooks, Sage 100, Procore"
                }
                ]}
                btnText="Request a Demo"
                btnClass="btn-primary"
            />
            <Breakout
                header="Accounting software that works as hard as you do"
                featuresList={['Simple, reliable, and secure', 'Easily manage cash flow', ' Be ready for tax time']}
                subtext="Never miss another detail. Time consuming and complex accounting is a task of the past."
            />
            <Content
                flip
                header="inBuild harnesses the power of artificial intelligence to capture all incoming financial documents."
                content={[
                {
                    header: "",
                    body: "On average, the quickest a trained bookkeeper can process one invoice into an ERP software is five minutes. By implementing our inBuild invoice process algorithm, we can cut down the time by 92%."
                },
                {
                    header: "",
                    body: "Instead of processing 12 invoices per hour using manual entry, try our algorithm and account for over 150 invoices in that same amount of time."
                }
                ]}
                image={homeImg1}
            />
            <TriColumn
                content={[
                {
                    header: "Robust Algorithm",
                    body: "Automate the invoice and accounting processes using machine learning. The invoice approval app provides your project managers and accountants with a simple interface for reviewing and approving invoices."
                },
                {
                    header: "Budgeting Optimization",
                    body: "Create, adjust, and finalize budgets all in one centralized location. Connect the company data to strengthen each new budget."
                },
                {
                    header: "Integration with your ERP",
                    body: "Streamlined integration with your enterprise resource planning (ERP) software and your construction management software (CMS)."
                }
                ]}
            />
            <Content
                header="Why inBuild?"
                content={[
                {
                    header: "Discover the aspects of inBuild’s software solutions",
                    body: "Construction finances are time-consuming, complex, and leave too much room for error. That’s why we developed inBuild."
                }
                ]}
                buttonToggle={true}
                buttonText="Schedule a Demo"
                image={homeImg2}
            />

        </div>
    )
}

export default Home
