import Subheader from '../components/Subheader';
import Content from '../components/content/Content';
import TriColumn from '../components/TriColumn';

import productImg1 from '../assets/product_img_01.png';
import productImg2 from '../assets/product_img_02.png';

function Product() {
    return (
        <div className="product-page">
            <Subheader
            subheader="Invoice processing with ease and accuracy."
            subtext="Where the software solution fits your business."
            />
            <Content
            content={[
                {
                header: "Email Extraction",
                body: "Our intelligent email extraction application allocates for the retrieval of invoice data from your email. Extract the data from each project and all of their invoices and completely automate the input process."
                },
                {
                header: "Detailed Invoice Analysis",
                body: "Once invoices are uploaded you are ready to analyze all the accounting aspects of each project. Get a clear picture of how each job is being paid for without missing any important information."
                },
                {
                header: "Invoice Submittals",
                body: "Each invoice is integrated into the submittal process where the invoice is then assigned to the correct approvers within the company. The invoice then moves on to the accounts payable step in the invoicing lifecycle."
                }
            ]}
            image={productImg1}
            />
            <TriColumn
            dark
            content={[
                {
                header: "Sign Up",
                body: "Speak with us today to set up an account for your business. Begin to save time and money by integrating inBuild."
                },
                {
                header: "Invoice",
                body: "Automate the invoice entry process by directly connecting your AP email address."
                },
                {
                header: "Optimize",
                body: "Improve your team's lead time by reducing the manual labor needed to enter and approve all incoming documents."
                }
            ]}
            />
            <Content
            flip
            content={[
                {
                header: "Approval Processing",
                body: "Depending on your unique company needs there can be multi-step approval flows that require previous approval before requesting a secondary review. This provides greater transparency and accountability."
                },
                {
                header: "Accounts Payable",
                body: "After approval, it is time to pay the vendors and subcontractors. This is where the outstanding amounts will be populated per project for easy accounting. Allowing your AP department to easily sync invoices with their accounting software."
                },
                {
                header: "Specific Project Reporting",
                body: "Each project is going to have unique aspects to analyze. This will give you a detailed overview and reporting data of all of the invoice and accounting aspects for those different projects."
                }
            ]}
            image={productImg2}
            />
        </div>
    )
}

export default Product
